import './View.css';
import {useState, useEffect} from 'react';
import { Container } from 'react-bootstrap';
import cloud from "../../resources/images/cloud.png"
import tree from "../../resources/images/tree.png"
import Namecard from '../../components/namecard/Namecard';

const scrollPercent = (scrollHeight:number) => {

    const screenHeight = window.innerHeight*0.5;
    if(scrollHeight >= screenHeight-50){
        return 1;
    }
    else{
        return scrollHeight/(screenHeight-50);
    }

}

const shift = (factor:number, constant:number ,scrollHeight:number) => {
    return (scrollPercent(scrollHeight)*factor)+constant;
}

const View = () => {

    const [scroll, setScroll] = useState(0);

    useEffect(() => {
        window.addEventListener("scroll", () => {
            setScroll(window.scrollY);
        });
    },[]);

    return (
        <Container fluid className="view-name-container">
            <Namecard/>
            <div className="background"></div>
            <div className="clouds">
                <img id="cloud1" style={{top:(-1*shift(0,200,scroll)),left:(-1*shift(0,300,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud2" style={{top:(-1*shift(100,100,scroll)),left:(-1*shift(100,200,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud3" style={{top:(-1*shift(200,0,scroll)),left:(-1*shift(200,100,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud4" style={{top:(-1*shift(0,200,scroll)),right:(-1*shift(0,300,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud5" style={{top:(-1*shift(100,100,scroll)),right:(-1*shift(100,200,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud6" style={{top:(-1*shift(200,0,scroll)),right:(-1*shift(200,100,scroll))}} src={cloud} alt="cloud"/>
                <img id="tree1" style={{bottom:(-1*shift(0,350,scroll)),left:(shift(300,-300,scroll))}} src={tree} alt="tree"/>
                <img id="tree2" style={{bottom:(-1*shift(0,350,scroll)),left:(shift(175,-150,scroll))}} src={tree} alt="tree"/>
                <img id="tree3" style={{bottom:(-1*shift(0,350,scroll)),left:(shift(50,0,scroll))}} src={tree} alt="tree"/>
                <img id="tree4" style={{bottom:(-1*shift(0,350,scroll)),right:(shift(300,-300,scroll))}} src={tree} alt="tree"/>
                <img id="tree5" style={{bottom:(-1*shift(0,350,scroll)),right:(shift(175,-150,scroll))}} src={tree} alt="tree"/>
                <img id="tree6" style={{bottom:(-1*shift(0,350,scroll)),right:(shift(50,0,scroll))}} src={tree} alt="tree"/>
            </div>
        </Container>
    );
}

export default View;