import './Menu.css';
import {Row, Col, Container } from 'react-bootstrap';

interface IProps {
    menuItems: string[]
}

const Menu = (props:IProps) => {
  return (
    <Container className="menu-container" fluid>
        <Row className="menu">
           {props.menuItems.map(item => (
               <Col key={item}>
                    {item}
               </Col>
           ))}
        </Row>
    </Container>
  );
}

Menu.defaultProps = {
    menuItems : ["Home", "About", "Github", "Contact"]
}

export default Menu;